<template>
  <div>
    <el-card>
      <div slot="header">
        基本信息
        <el-button
          style="float: right"
          type="info"
          size="small"
          @click="$router.go(-1)"
          >返回</el-button
        >
      </div>
      <div>
        <!-- :rules="rules" -->
        <el-form
          :model="Form"
          ref="formRef"
          :rules="rule"
          label-width="200px"
          class="demo-ruleForm"
        >
          <el-row :gutter="10">
            <el-col :span="12">
              <el-form-item label="负责人" required prop="peoId">
                <el-select
                  v-model="Form.peoId"
                  filterable
                  placeholder="请输入内容"
                >
                  <el-option
                    v-for="item in Options"
                    :key="item.id"
                    :label="item.teaName"
                    :value="item.teaId"
                  >
                  </el-option>
                </el-select>
              </el-form-item>
            </el-col>

            <el-col :span="12">
              <el-form-item label="著作名称" required prop="wrName">
                <el-input v-model="Form.wrName"></el-input>
              </el-form-item>
            </el-col>
          </el-row>
          <el-row :gutter="10">
            <el-col :span="12">
              <el-form-item v-if="this.istea == 1" label="所属单位" prop="crBelongDept" required>
                <el-input v-model="Form.belongDept"></el-input>
              </el-form-item>
              <el-form-item v-else label="所属学院" prop="crBelongDept">
                <el-input v-model="Form.belongDept"></el-input>
              </el-form-item>
            </el-col>
            <el-col :span="12">
              <el-form-item label="出版单位" required prop="wrPublishUnit">
                <el-input v-model="Form.wrPublishUnit"></el-input>
              </el-form-item>
            </el-col>
          </el-row>

          <el-row :gutter="10">
            <el-col :span="12">
              <el-form-item label="出版时间" required prop="wrPublishTime">
                <el-input v-model="Form.wrPublishTime"></el-input>
              </el-form-item>
            </el-col>

            <el-col :span="12">
              <el-form-item
                label="出版地"
                required
                prop="wrPublishPlace"
              >
              <el-select v-model="Form.wrPublishPlace">
                  <el-option label="国内" value="国内"></el-option>
                  <el-option label="国外" value="国外"></el-option>
                </el-select>
              </el-form-item>
            </el-col>
          </el-row>

          <el-row :gutter="10">
            <el-col :span="12">
              <el-form-item
                label="著作类别"
                required
                prop="wrType"
              >
              <el-select v-model="Form.wrType">
                  <el-option label="工具书" value="工具书"></el-option>
                  <el-option label="专著" value="专著"></el-option>
                  <el-option label="编著" value="编著"></el-option>
                  <el-option label="译著" value="译著"></el-option>
                  <el-option label="教材" value="教材"></el-option>
                  <el-option label="皮书/发展报告" value="皮书/发展报告"></el-option>
                  <el-option label="科普读物" value="科普读物"></el-option>
                </el-select>
              </el-form-item>
            </el-col>
            <el-col :span="12">
              <el-form-item label="学科门类" required prop="subjectCategory">
                <el-select v-model="Form.subjectCategory">
                  <el-option label="科技类" value="科技类"></el-option>
                  <el-option label="社科类" value="社科类"></el-option>
                </el-select>
              </el-form-item>
            </el-col>
          </el-row>

          <el-row :gutter="10">
            <el-col :span="12">
              <el-form-item
                label="一级学科"
                required
                prop="firstLevelDiscipline"
              >
                <el-input v-model="Form.firstLevelDiscipline"></el-input>
              </el-form-item>
            </el-col>
            <el-col :span="12">
              <el-form-item label="项目来源" required prop="projectSource">
                <el-input v-model="Form.projectSource"></el-input>
              </el-form-item>
            </el-col>
          </el-row>

          <el-row :gutter="10">
            <el-col :span="12">
              <el-form-item label="字数：（单位：万字）" required prop="wordCount">
                <el-input v-model="Form.wordCount"></el-input>
              </el-form-item>
            </el-col>
            <el-col :span="12">
              <el-form-item label="是否译成外文" required prop="isTranslation">
                <el-select v-model="Form.isTranslation">
                  <el-option label="是" value="是"></el-option>
                  <el-option label="否" value="否"></el-option>
                </el-select>
              </el-form-item>
            </el-col>
          </el-row>

          <el-row :gutter="10">
            <el-col :span="12">
              <el-form-item label="学校署名" required prop="schoolSignature">
                <el-select v-model="Form.schoolSignature">
                  <el-option label="第一单位" value="第一单位"></el-option>
                  <el-option label="非第一单位" value="非第一单位"></el-option>
                </el-select>
              </el-form-item>
            </el-col>
            <el-col :span="12">
              <el-form-item label="语种" required prop="wrLanguage">
                <el-select v-model="Form.wrLanguage">
                  <el-option label="中文" value="中文"></el-option>
                  <el-option label="英语" value="英语"></el-option>
                  <el-option label="俄语" value="俄语"></el-option>
                  <el-option label="葡萄牙语" value="葡萄牙语"></el-option>
                  <el-option label="波兰语" value="波兰语"></el-option>
                  <el-option label="拉丁语" value="拉丁语"></el-option>
                  <el-option label="朝鲜语" value="朝鲜语"></el-option>
                  <el-option label="日语" value="日语"></el-option>
                  <el-option label="希伯来语" value="希伯来语"></el-option>
                  <el-option label="意大利语" value="意大利语"></el-option>
                  <el-option label="匈牙利语" value="匈牙利语"></el-option>
                  <el-option label="法语" value="法语"></el-option>
                  <el-option label="西班牙语" value="西班牙语"></el-option>
                  <el-option label="德语" value="德语"></el-option>
                  <el-option label="阿拉伯语" value="阿拉伯语"></el-option>
                  <el-option label="其他" value="其他"></el-option>
                </el-select>
              </el-form-item>
            </el-col>
          </el-row>

          <el-row :gutter="10">
            <el-col :span="12">
              <el-form-item
                label="ISSN号"
                required
                prop="issnNumber"
              >
                <el-input v-model="Form.issnNumber"></el-input>
              </el-form-item>
            </el-col>
            <el-col :span="12">
              <el-form-item
                label="CIP号"
                required
                prop="cipNumber"
              >
                <el-input v-model="Form.cipNumber"></el-input>
              </el-form-item>
            </el-col>
          </el-row>

          <el-row :gutter="10">
            <el-col :span="12">
              <el-form-item label="出版社级别" prop="publisherLevel">
                <el-select v-model="Form.publisherLevel">
                  <el-option label="一级" value="一级"></el-option>
                  <el-option label="二级" value="二级"></el-option>
                  <el-option label="三级" value="三级"></el-option>
                  <el-option label="其他" value="其他"></el-option>
                </el-select>
              </el-form-item>
            </el-col>

            <el-col :span="12">
              <el-form-item label="简介" prop="wrIntro">
                <el-input v-model="Form.wrIntro"></el-input>
              </el-form-item>
            </el-col>
          </el-row>
        </el-form>
      </div>
    </el-card>

    <el-card>
      <div slot="header">
        作者信息
        <!-- 新增 -->
        <span>
          <el-tooltip
            effect="dark"
            content="新增"
            placement="right"
            :enterable="false"
          >
            <!-- v-if="is_authority.isUserDel" -->
            <el-button
              class="add"
              type="primary"
              icon="el-icon-plus"
              v-if="is_authority.is_user_add"
              size="mini"
              @click="authAdd"
              >点击增加</el-button
            >
          </el-tooltip>
        </span>
      </div>
      <div>
        <el-table :data="authForm" border stripe>
          <el-table-column prop="authorType" label="作者类型" align="center" width="150px">
            <template slot-scope="scope">
              <el-select v-model="scope.row.authorType">
                <el-option
                  label="本校老师"
                  value="本校老师">
                </el-option>
                <el-option
                  label="本校学生"
                  value="本校学生">
                </el-option>
                <el-option
                  label="外校人员"
                  value="外校人员">
                </el-option>
              </el-select>
            </template>
          </el-table-column>

          <el-table-column prop="authorName" label="* 作者姓名" align="center">
            <template slot-scope="scope">
              <el-input v-model="scope.row.authorName"></el-input>
            </template>
          </el-table-column>

          <el-table-column
            width="150"
            prop="authorId"
            label="作者编号"
            align="center"
          >
            <template slot-scope="scope">
              <el-input v-model="scope.row.authorId"></el-input>
            </template>
          </el-table-column>

          <el-table-column
            width="80"
            prop="authorSex"
            label="性别"
            align="center"
          >
            <template slot-scope="scope">
              <el-input v-model="scope.row.authorSex"></el-input>
            </template>
          </el-table-column>

          <el-table-column prop="authorEducation" label="学历" align="center">
            <template slot-scope="scope">
              <el-select v-model="scope.row.authorEducation" placeholder="请选择学历">
                  <el-option
                    label="博士研究生"
                    value="博士研究生">
                  </el-option>
                  <el-option
                    label="硕士研究生"
                    value="硕士研究生">
                  </el-option>
                  <el-option
                    label="本科"
                    value="本科">
                  </el-option>
                  <el-option
                    label="专科"
                    value="专科">
                  </el-option>
                  <el-option
                    label="高中及其以下"
                    value="高中及其以下">
                  </el-option>
                  <el-option
                    label="其他"
                    value="其他">
                  </el-option>
                </el-select>
            </template>
          </el-table-column>

          <el-table-column
            width="100"
            prop="signingOrder"
            label="* 署名顺序"
            align="center"
          >
            <template slot-scope="scope">
              <el-input
                v-model="authForm[scope.$index].signingOrder"
              ></el-input>
            </template>
          </el-table-column>

          <el-table-column prop="authorRole " label="承担角色" align="center">
            <template slot-scope="scope">
              <el-input v-model="scope.row.authorRole   "></el-input>
            </template>
          </el-table-column>

          <el-table-column v-if="this.istea == 1" prop="authorTitle" label="职称" align="center">
            <template slot-scope="scope">
              <el-input v-model="scope.row.authorTitle"></el-input>
            </template>
          </el-table-column>

          <el-table-column
            prop="authorWorkUnit"
            label="工作单位"
            align="center"
          >
            <template slot-scope="scope">
              <el-input v-model="scope.row.authorWorkUnit"></el-input>
            </template>
          </el-table-column>

          <el-table-column
            prop="contributionRate"
            label="参编字数（单位：万字）"
            align="center"
            ><template slot-scope="scope">
              <el-input
                v-model="scope.row.charactersNumber"
              ></el-input> </template
          ></el-table-column>

          <el-table-column label="操作" width="120px" align="center">
            <template slot-scope="scope">
              <!-- 删除 -->
              <el-tooltip
                class="item"
                effect="dark"
                content="删除"  v-if="is_authority.is_user_del"
                placement="top-start"
                :enterable="false"
              >
                <!-- v-if="is_authority.isUserDel" -->
                <el-button
                  type="danger"
                  icon="el-icon-delete"
                  size="mini"
                  @click="delAuthRow(scope, authForm)"
                ></el-button>
              </el-tooltip>
            </template>
          </el-table-column>
        </el-table>
      </div>
      <el-button class="save" type="primary" @click="submitForm"
        >保存
      </el-button>
    </el-card>

    <el-card>
      <div slot="header">
        附件信息

        <el-upload
          :file-list="fileList"
          class="upload-demo"
          action="http://www.dayanhulian.com:8001/matter/fileoss/uploadOssFile"
          multiple
          :headers="config"
          :on-success="saveUrl"
          :before-upload="beforeUpload"
          accept=".jsp,.png"
        >
          <el-button icon="el-icon-upload2" size="small" type="primary"
            >点击上传</el-button>
          <span slot="tip" class="el-uploaddTip">只能上传jpg/png文件</span>
        </el-upload>
      </div>

      <el-dialog
          :visible.sync="dialogVisible">
          
          <img width="100%" :src="photoView" >

        </el-dialog>


      <div
      v-if="this.Id"
      >
        <el-table :data="accessory" border stripe>
          <el-table-column label="附件" align="center">
            <template slot-scope="scope">
              <img @click="photo(scope.row)" :src="scope.row.url" alt=""  style="width: 80px;height: 80px">
            </template>
          </el-table-column>
          
          <el-table-column label="操作" width="180px" align="center">
            <template slot-scope="scope">
              <el-tooltip
                class="item"
                effect="dark"
                content="预览"
                placement="top-start"
                :enterable="false"
              >
                  <el-button
                    @click="photo(scope.row)"
                    type="primary"
                    icon="el-icon-view"
                    size="mini"
                  ></el-button>
              </el-tooltip>
              
              <el-tooltip
                class="item"
                effect="dark"
                content="下载"
                placement="top"
                :enterable="false"
              >
                  <a :href="scope.row.url">
                      <el-button
                          class="download"
                          type="primary"
                          icon="el-icon-download"
                          size="mini"
                    ></el-button>
                  </a>
              </el-tooltip>


              <el-tooltip
                class="item"
                effect="dark"
                content="删除"  v-if="is_authority.is_user_del"
                placement="top-start"
                :enterable="false"
              >
                <el-button
                  type="danger"
                  icon="el-icon-delete"
                  size="mini"
                  @click="delAccessory(scope, accessory)"
                ></el-button>
              </el-tooltip>
            </template>
          </el-table-column>
        </el-table>
      </div>
    </el-card>
  </div>
</template>

<script>
import { mapState } from 'vuex'
export default {
  data() {
    return {
      is_authority: {
        is_user_del: false,
        is_user_upt: false,
        is_user_add: false,
        is_user_add_role: false
      },
      rule:{
        peoId: [
          { required: true, message: '该项不能为空', trigger: 'blur' }
        ],
        wrName: [
          { required: true, message: '该项不能为空', trigger: 'blur' }
        ],
        crBelongDept: [
          { required: true, message: '该项不能为空', trigger: 'blur' }
        ],
        wrPublishUnit: [
          { required: true, message: '该项不能为空', trigger: 'blur' }
        ],
        wrPublishTime: [
          { required: true, message: '该项不能为空', trigger: 'blur' }
        ],
        wrPublishPlace: [
          { required: true, message: '该项不能为空', trigger: 'blur' }
        ],
        wrType: [
          { required: true, message: '该项不能为空', trigger: 'blur' }
        ],
        subjectCategory: [
          { required: true, message: '该项不能为空', trigger: 'blur' }
        ],
        firstLevelDiscipline: [
          { required: true, message: '该项不能为空', trigger: 'blur' }
        ],
        projectSource: [
          { required: true, message: '该项不能为空', trigger: 'blur' }
        ],
        wordCount: [
          { required: true, message: '该项不能为空', trigger: 'blur' }
        ],
        isTranslation: [
          { required: true, message: '该项不能为空', trigger: 'blur' }
        ],
        schoolSignature: [
          { required: true, message: '该项不能为空', trigger: 'blur' }
        ],
        wrLanguage: [
          { required: true, message: '该项不能为空', trigger: 'blur' }
        ],
        issnNumber: [
          { required: true, message: '该项不能为空', trigger: 'blur' }
        ],
        cipNumber: [
          { required: true, message: '该项不能为空', trigger: 'blur' }
        ],
        publisherLevel: [
          { required: true, message: '该项不能为空', trigger: 'blur' }
        ],
        financialAccount: [
          { required: true, message: '该项不能为空', trigger: 'blur' }
        ],
        supportingExpenses: [
          { required: true, message: '该项不能为空', trigger: 'blur' }
        ],
        seltFinancing: [
          { required: true, message: '该项不能为空', trigger: 'blur' }
        ],
        specialFunds: [
          { required: true, message: '该项不能为空', trigger: 'blur' }
        ]
      },
      dialogVisible: false,
      fileList: [],
      accessory: [],
      Options: [],
      Form: {},
      authForm: [],
      List: [],
      istea: this.$route.query.istea,
      token: window.sessionStorage.getItem('token'),
      Id: this.$route.query.id,
      newAddId: undefined,
      photoView:'',
    }
  },
  computed: {
    ...mapState(['btus']),
    config() {
      return { Token: this.token }
    }
  },
  created() {
    this.isAuthority()
    this.getDetail()
    this.getTreeselect()
  },
  
  methods: {
    isAuthority() {
      var arr = this.btus.split(',')
      for (var key in this.is_authority) {
        if (arr.indexOf(key) != -1) {
          this.is_authority[key] = true
        }
      }
    },
    photo(row){
      this.dialogVisible = true
      this.photoView=row.url
    },
    beforeUpload(file){
      if (!this.Id && !this.newAddId) {
        this.$message.error('请先保存作者信息!')
        return false
      }else{
        console.log(file.type);
        const isjpg = file.type === 'image/png' ||file.type === 'image/jpeg';
        if (!isjpg) {
          this.$message.error('请上传 JPG或者PNG 格式!');
        }
        return isjpg
      }
    },
    async saveUrl(response, file, fileList) {
      this.$http.post('/accessoriesList/saveOrUpdateAccessoriesList', {
        proId: this.Id || this.newAddId,
        url: response.data.url
      })
      this.getDetail()
    },

    authAdd() {
      this.authForm.splice(this.authForm.length, 0, {
        authorEducation: "",
        authorId: "",
        authorName: "",
        authorSex: "",
        authorTitle: "",
        authorType: "",
        authorWorkUnit: null,
        contributionRate: "",
        id: "",
        wrId: this.$route.query.id,
        signingOrder: 1,
      })
    },

    delAccessory(scope, rows) {
      const userid = scope.row.id.split(',')
      console.log(userid)
      this.$confirm('确认要删除吗?', '警告', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      })
        .then(async () => {
          await this.$http.delete(
            '/accessoriesList/removeByIdAccessoriesList/' + userid
          )
          // console.log(res);
          this.getDetail()
        })
        .then(() => {
          this.$message({
            showClose: true,
            message: '删除成功',
            type: 'success'
          })
        })
    },

    delAuthRow(scope, rows) {
      // console.log(scope);
      if (scope.row.id) {
        const userid = scope.row.id.split(',')
        console.log(userid)
        this.$confirm('确认要删除吗?', '警告', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        })
          .then(async () => {
            await this.$http.delete(
              '/worksResultsAuthorInfo/removeByIdWorksResultsAuthorInfos',
              {
                data: userid
              }
            )
            // console.log(res);
            this.getDetail()
            this.Form = []
          })
          .then(() => {
            this.$message({
              showClose: true,
              message: '删除成功',
              type: 'success'
            })
          })
      } else {
        rows.splice(scope.$index, 1)
      }
    },

    async getTreeselect() {
      this.istea = this.$route.query.istea
      const id = this.$route.query.id
      // console.log(istea);
      if (this.istea == 1) {
        const { data: res } = await this.$http.post(
          '/teaBaseInfo/queryTeaBaseInfoList',
          {
            page: {
              current: 1,
              size: 999
            }
          }
        )
        this.Options = res.data.dataList.records
        if (res.code != 20000) return this.$message.error(res.message)
        // console.log(this.Options)
        // console.log(this.majorOptions)
      } else if (this.istea == 0) {
        const { data: res } = await this.$http.post(
          '/studentInfo/queryStudentInfoList',
          {
            page: {
              current: 1,
              size: 999
            }
          }
        )
        let a = res.data.dataList.records
        a = JSON.parse(JSON.stringify(a).replace(/stuId/g, 'teaId'))
        this.Options = JSON.parse(
          JSON.stringify(a).replace(/stuName/g, 'teaName')
        )
        if (res.code != 20000) return this.$message.error(res.message)
        // console.log(this.Options)
        // console.log(this.majorOptions)
      }
    },

    async getDetail() {
      // console.log(this.Id);
      if (!this.Id) {
        return
      }
      // console.log(id)
      const { data: res } = await this.$http.get(
        '/worksResultsInfo/getByIdWorksResultsInfo/' + this.Id
      )
      if (res.code != 20000) return this.$message.error(res.message)
      this.Form = res.data.entity
      console.log(this.Form)
      this.getAutherinfo()
      this.getAccessory()
    },

    async getAccessory() {
      const { data: res } = await this.$http.get(
        '/accessoriesList/getByIdAccessoriesList/' + this.Form.id
      )
      if (res.code != 20000) return this.$message.error(res.message)
      this.accessory = res.data.entity
      console.log(this.accessory)
    },

    async getAutherinfo() {
      const { data: res } = await this.$http.get(
        '/worksResultsAuthorInfo/queryWorksResultsAuthorInfoList/' + this.Form.id
      )
      if (res.code != 20000) return this.$message.error(res.message)
      this.authForm = res.data.dataList
      console.log(this.authForm)
    },
    submitForm() {
      if (this.istea == 1) this.Form.isStu = 1
      else if (this.istea == 0) this.Form.isStu = 0
      this.$refs.formRef.validate(async valid => {
        if (valid) {
          for (let index = 0; index < this.authForm.length; index++) {
            if (!this.authForm[index].authorName  || !this.authForm[index].signingOrder) {
              return this.$message.error('请输入作者姓名和作者署名')
            }

          }
          await this.$http.post('/worksResultsInfo/saveOrUpdateWorksResultsInfo', this.Form)

          if (!this.Id && this.istea == 1) {
            const { data: res } = await this.$http.post(
              '/worksResultsInfo/queryTeaWorksResultsInfoList',
              {
                page: {
                  current: 1,
                  size: 999
                }
              }
            )
            this.newAddId = res.data.dataList.records.pop().id
            // console.log(Id)
          } else if (!this.Id && this.istea == 0) {
            const { data: res } = await this.$http.post(
              '/worksResultsInfo/queryStuWorksResultsInfoList',
              {
                page: {
                  current: 1,
                  size: 999
                }
              }
            )
            this.newAddId = res.data.dataList.records.pop().id
          }

          for (let index = 0; index < this.authForm.length; index++) {
            if (!this.Id) {
              this.authForm[index].wrId = this.newAddId
              this.authForm[index].signingOrder = 1
            }
            var { data: res1 } = await this.$http.post(
              '/worksResultsAuthorInfo/saveOrUpdateWorksResultsAuthorInfo',
              this.authForm[index]
            )
          }
          if(!res1) return this.$message.error('请填写作者信息!')
          if (res1.code != 20000) return this.$message.error(res1.message)
          else {
            return this.$message.success(res1.message)
            this.getDetail()
            this.$router.go(-1)
          }
        }
      })
    }
  }
}
</script>
